                it: `No cappelli o il velo (a Meno che non indossati quotidianamente per motivi religiosi o per scopi medici. Presentare una dichiarazione firmata che verifica che il cappello o copricapo  parte del riconosciuto, religiosa tradizionale abbigliamento abitualmente o che devono essere indossati continuamente in pubblico o sottoscritta la dichiarazione del medico verificare l'elemento viene usato quotidianamente per scopi medici. Il pieno viso deve essere visibile e il suo cappello o copricapo non pu nascondere l'attaccatura dei capelli o gettare ombre sul tuo viso.)`,
